<template>
    <header><h1>{{ titleHeader }}</h1><br>
    <button v-on:click="changeTitleHeader">Change Title from CompHeader</button>
    </header>
</template>

<script>
export default {
    name: 'comp-header',
  props: {
      titleHeader: String,
    title: String
  },
    data() {
        return {

        }
    },
  methods: {
      changeTitleHeader(e) {
        // Khng c php thay i trc tip props t Compnent Cha
        // Truyn s kin thng bo ra bn ngoi
        // Kch hot s kin 'changeTitleEvent' trong CompHeader.vue
        // Khi kch hot function bn ngoi app.vue n s chy
        // Khi function  c chy , n s thay i d liu
      var data = {
          title: 'Data truyn thng qua bin event'
        }
        this.$emit('changeTitleEvent', data)
      }
  }
}
</script>

<style>

</style>



